import React from "react";
import MainHOC from "../component/main.hoc";

const SearchJud = () => {
  return (
    <MainHOC>
      <div className="hero-body is-fullheight">
        <div className="container">
          <div className="columns is-vcentered">
            <div className="column is-12 is-offset-1 landing-caption">
              <label className="label is-large">
                ค้นหาข้อมูลเงินเดือน ตั้งแต่ &nbsp;
                <font color="red">มกราคม 2563 </font>
              </label>
              <div className="field is-horizontal">
                <div className="field-label is-normal">
                  <label className="label">ประเภทเงิน</label>
                </div>
                <div className="field-body">
                  <div className="field is-narrow">
                    <div className="control">
                      <div className="select is-fullwidth">
                        <select>
                          <option>เลือกประเภทเงิน</option>
                          <option value="1">ตกเบิก</option>
                          <option value="2">เงินเดือน</option>
                        </select>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="field is-horizontal">
                <div className="field-label is-normal">
                  <label className="label">ประจำเดือน</label>
                </div>
                <div className="field-body">
                  <div className="field is-narrow">
                    <div className="control">
                      <div className="select is-fullwidth">
                        <select>
                          <option>เลือกเดือน</option>
                          <option value="1">มกราคม</option>
                          <option value="2">กุมภาพันธ์</option>
                          <option value="3">มีนาคม</option>
                          <option value="4">เมษายน</option>
                          <option value="5">พฤษภาคม</option>
                          <option value="6">มิถุนายน</option>
                          <option value="7">กรกฎาคม</option>
                          <option value="8">สิงหาคม</option>
                          <option value="9">กันยายน</option>
                          <option value="10">ตุลาคม</option>
                          <option value="11">พฤศจิกายน</option>
                          <option value="12">ธันวาคม</option>
                        </select>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
                <div className="field is-horizontal">
                  <div className="field-label is-normal">
                    <label className="label">ปี</label>
                  </div>
                  <div className="field-body">
                    <div className="field">
                      <div className="control">
                        <input
                          className="input"
                          type="text"
                          defaultValue="2563"
                          readOnly
                        />
                      </div>
                    </div>
                  </div>
                </div>
                <div className="field is-horizontal">
                  <div className="field-label is-normal"></div>
                  <div className="field-body">
                    <div className="field">
                      <button class="button is-primary">ค้นหา</button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
                    <div className="column is-5 text-align-top">
              <div>

                <h2 className="subtitle" style={{ marginTop: 20, color: "#f39200" }}>
                  ระบบพิมพ์สลิปเงินเดือน <a>ข้าราชการตุลาการ</a> ศาลยุติธรรม
                </h2>
                <article className="panel is-warning">
                  <p className="panel-heading">
                    เข้าสู่ระบบ ({selectLogin})
                </p>

                  <div className="panel-block">

                    <p className="control has-icons-left">
                      <input className="input" type="text" placeholder="Username" required />
                      <span className="icon is-small is-left">
                        <i className="fas fa-user" />
                      </span>
                    </p>
                  </div>
                  <div className="panel-block">

                    <p className="control has-icons-left has-icons-right">
                      <input className="input" type="password" placeholder="Password" required />
                      <span className="icon is-small is-left">
                        <i className="fas fa-lock" />
                      </span>

                      <span className="icon is-small is-right" >
                        <i className="fas fa-eye" />
                      </span>
                    </p>
                  </div>

                  <div className="panel-block is-right">
                    <div className="buttons is-right">
                      <button className="button is-link">เข้าสู่ระบบ </button><br />
                    </div>
                  </div>
                </article>
              </div>
              <hr></hr>
              <div className="field">
                {title}
              </div>

            </div>
    </MainHOC>
  );
};
export default SearchJud;
